import React from "react";
import introImg from "../../assets/images/intro-img.jpg";
import "./Intro.css";
function Intro() {
  return (
    <div className="Intro-wrapper" id="section-intro">
      <div className="container">
        <div className="row">
          <div className="col-md-8">
            <h1 className="font-playfair intro-header">
              Self-driven and proactive Entry level Software Engineer with a
              passion for creative problem-solving methods.
            </h1>
          </div>
        </div>
        <div className="row">
          <div className="col-md-8">
            <div className="intro-content">
              <p>
                Hello! I'm Saurav, undergrad at Thapar Institute of Engineering
                and Technology who's experienced in taking web applications from
                scratch to production.
              </p>
              <p>
                As a freelancer/intern, I've had the opportunity to develop for
                a handful of awesome companies / clients and continue to do so
                today.Adept at identifying innovative solutions in collaboration
                with diverse teams and project stakeholders, defining user
                requirements, and driving customer scalability.
              </p>
              <img src={introImg} alt="Office Image" />
            </div>
          </div>
          <div className="col-md-3 ">
            <div className="intro-side ">
              <h4>HELLO! I’M SAURAV</h4>
              <p>
                Entry level <span>Software Engineer</span> with electronics
                background and seeking to utilize broad education background
                with excellent analytical, technical, and programming skills to
                thrive as an entry-level software engineer
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Intro;
